<template>
  <div>
    <p class="text-center font-weight-bold mt-8">{{ content.title }}</p>
    <p class="text-center grey--text .body-2 mt-1">
      <span class="mr-3">发布企业: {{ content.publisher }}</span>
      <span class="mx-3">发布时间：{{ content.time }}</span>
      <span class="ml-3">点击量: {{ content.click }}</span>
    </p>
    <div class="dotted_line mt-3 mx-5"></div>
    <p class="text-left mx-12 mt-12">
      <span class="text-left font-weight-bold">项目简介：</span>
      <span>
        {{ content.intro }}
      </span>
    </p>
    <p class="text-left mx-12 mt-2">
      <span class="text-left font-weight-bold">项目预算：</span>
      <span>{{ content.budget }}元</span>
    </p>
    <p class="text-left mx-12 mt-2" v-if="content.fileurl">
      <span class="text-left font-weight-bold">需求文档：</span>
      <a :href="content.fileurl">附件下载：{{ content.filename }}</a>
    </p>
    <div class="text-center mt-3">
      <div class="text-center">
        <v-dialog v-model="dialog" width="500">
          <template v-slot:activator="{ on }">
            <v-btn color="indigo" dark v-on="on" min-width="300"
              >企业联系方式</v-btn
            >
          </template>

          <v-card>
            <v-card-title class="grey lighten-2" primary-title
              >联系方式</v-card-title
            >

            <v-card-text class="mt-4 body-1">
              <p>
                <span>联系电话：</span>
                {{ content.tel }}
              </p>
              <p>
                <span>联系邮箱：</span>
                {{ content.mail }}
              </p>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="primary" text @click="dialog = false">确认</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </div>
    </div>
    <div class="dotted_line mt-3 mx-5"></div>
    <div class="ml-8 mt-8">
      <p
        @click="handleToDetail(last.id || '')"
        style="cursor: pointer;"
        class="lastAndNext"
      >
        <span class="grey--text">上一条：</span>
        {{ last.title || '' }}
      </p>
      <p
        @click="handleToDetail(next.id || '')"
        style="cursor: pointer;"
        class="lastAndNext"
      >
        <span class="grey--text">下一条：</span>
        {{ next.title || '' }}
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'detail',
  props: ['subject', 'id'],
  data() {
    return {
      content: {},
      last: {
        title: '',
        id: '',
      },
      next: {
        title: '',
        id: '',
      },
      dialog: false,
    }
  },
  watch: {
    $route() {
      this.getDemand()
    },
  },
  methods: {
    isUndef(obj) {
      if (obj === undefined) {
        return true
      } else return false
    },
    async getDemand() {
      var enterprise = this.$route.query.enterprise
      if (this.isUndef(this.$route.query.enterprise)) {
        enterprise = ''
      }
      var year = this.$route.query.year
      if (this.isUndef(this.$route.query.year)) {
        year = ''
      }
      let res = await this.$http.get(
        '/api/school/readEntry?id=' +
          this.id +
          '&need=' +
          this.subject +
          '&enter=' +
          enterprise +
          '&year=' +
          year
      )
      if (res.status != 200) return false
      if (res.data.code != 200) return false
      this.content = res.data.data.Enterpriseneeds
      if (res.data.data.Enterlast) {
        this.last.title = res.data.data.Enterlast.title
        this.last.id = res.data.data.Enterlast.id
      }
      if (res.data.data.Enternext) {
        this.next.title = res.data.data.Enternext.title
        this.next.id = res.data.data.Enternext.id
      }
    },
    handleToDetail(id) {
      this.$router.push({
        path: '/university/demand/list/' + this.subject + '/detail/' + id,
        query: {
          enterprise: this.$route.query.enterprise,
          year: this.$route.query.year,
        },
      })
    },
  },
  mounted() {
    this.getDemand()
  },
}
</script>

<style scoped>
.dotted_line {
  width: 98%;
  height: 0;
  border-bottom: #c6c6c6 1px dashed;
}
.lastAndNext:hover {
  color: #e53935;
}
</style>
